.header {
    height: 5rem;
    width: 12rem;

    background: #362030;
    margin-bottom: 1rem;
    border-bottom: 1px #050204 solid;

    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebarContainer {
    flex-direction: column;

    background: #53354a;

    width: 12rem;
    height: 100vh;


    a {
        text-decoration: none;

    }

    .menu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    button {
        border: none;
        background: none;
        width: 100%;

        cursor: pointer;
        color: white;
    }

    .buttonMain {
        height: 3rem;

        margin: 1rem 0;

        display: flex;
        align-items: center;
        justify-content: space-between;

        background: #e84545;


        &:hover {
            transition: 0.2s;
            background: #972727;
        }

        .buttonMainBtn {
            font-weight: bold;
            font-size: 1.5rem;

            margin-right: auto;
        }

        span {
            color: white;
            margin-left: auto;
        }
    }
}
