.main {
    margin: 1rem 1.5rem;

    display: flex;
    flex-wrap: wrap;

    .geralContainer {
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
    }

    section {
        margin: 1rem 0;

        width: 25rem;
        height: 25rem;

        background: #c31432;
        background: -webkit-linear-gradient(to right, #240b36, #c31432);
        background: linear-gradient(to right, #240b36, #c31432);

        color: white;

        border-radius: 25px;

        .timeConfigContainer {
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            width: 80%;

            padding: 2rem 5rem 0rem 2rem;
        }

        .messageContainer {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;

            height: 100%;
        }
    }

    #dataContainer {
        background: white;
        color: black;
    }
}
