.geralContainer{
    
    min-height: 25rem;
    
    text-align: center;

    h1{
        padding: 3rem 0;
    }

    .divForm{
        min-width: fit-content;
        max-width: 35rem;
        min-height: 25rem;
        max-height: fit-content;

        display: flex;
        flex-direction: column;

        margin: auto;

        background-color: aliceblue;

        border-radius: 20px;
    }

    form{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
    }

    input{
        margin: 1rem 0;
        height: 2rem;
        width: 20rem;
        border-radius: 5px;
    }

    button{
        background: #e84545;
        color:white;
        font-weight: bold;


        border:none;
        border-radius: 5px;


        width: 6rem;
        height: 2rem;

        cursor: pointer;

        &:hover{
            transition: .2s;
            background: #f87575;
        }
    }


    .taskNumber{
        font-weight: bold;
        background-color: rgb(85, 171, 228);
        width: fit-content;
        padding: 1rem;
        margin: 0 auto;
        border-radius: 5px;
        color:white;
    }

    ul{
        list-style: none;
        margin-top: 2rem;

        div{
          display: flex;
          flex-wrap: wrap;  
          align-items: center;
          justify-content: space-between;

          background-color: rgb(51, 112, 165);
          margin: 0.5rem 1rem;
          padding: .3rem;

          border-radius: 10px;

          li{
            text-align: start;
            text-indent: 2rem;
            
            font-size: 1.2rem;
            color: white;

        }
        span{
            margin-right: 1rem;
            color: white;
            background-color: rgb(165, 78, 51);
            cursor: pointer;
            
            font-weight: bolder;
            font-size: 1.2rem;
            
            padding: .5rem .9rem;

            border-radius: 100%;

            &:hover{
                transition: .2s;
                background: rgb(190, 100, 73);
            }
        }

        }
        

    }

}