.loginDiv{
    background: #723844;

    border-radius: 20px;

    width: 25rem;
    height: 30rem;
    margin: 5% auto;

    display: flex;
    flex-direction: column;

    padding: 2rem;

    h1{
        margin: 2rem auto;
        font-size: 2.5rem;

    }

    form{
        display: flex;
        flex-direction: column;
        
        padding: 0 2rem;
    }

    label{
        display: flex;
        flex-direction: column;

        margin: 1rem 0;

        font-weight: bold;
        font-size: 1.1rem;

        input{
            margin: .2rem 0;
            padding: .3rem;

            border-radius: 10px;

            border:none;


        }
    }

    button{
        width: 8rem;
        height: 2.5rem;

        background: #455de8;
        color: white;

        font-weight: bold;
        font-size: 1.3rem;

        border-radius: 10px;
        border:none;

        cursor: pointer;

        margin: 1rem 1.2rem;

        &:hover{
            transition: .2s;
            background: #6b7de4;
            transform: scale(1.1);
        }

        &:active{
            transition: .2s;
            background: #27378f;
            transform: scale(.98);
        }
    }
    .buttonContainer{    
    #buttonRegister{
        background: none;

        border:solid 2px #455de8;

        font-weight: normal;

        &:hover{
            transition: background .3s,font-weight .1s;

            font-weight: bold;

            border:none;
            background: #455de8;

        }
    }
    }
    
}