.geralContainer{
    width: 100%;

   
    
    .navContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    height: 5rem;
    width: 100%;

    background: #903749;


    button{
        background: #e84545;
        color:white;
        font-weight: bold;


        border:none;
        border-radius: 25px;

        margin-left: 1.5rem;

        width: 6rem;
        height: 2rem;

    }


    .rightContainer{
        display: flex;
        align-items: center;


        .userContainer{
            display: flex;
            align-items: center;

            padding-right: 2rem;

        img{
            background: sandybrown;
            border-radius: 10px;
            border:rgb(154, 135, 236) 1px solid;
            height: 2rem;
            width: 2rem;
            margin: 0 1rem;

        }
        }

    }


    .iconsContainer{
        .icon{
            padding: 0 1rem;
        }

        .icon:last-child{
            padding: 0;
        }
    }
}
}